import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController, NavController } from '@ionic/angular';
import { EDGuardarPermisoTrabajoAPP } from 'src/app/interfaces/edguardar-permiso-trabajo-app';
import { DataBaseService } from 'src/app/services/SST/data-base.service';
import { DataService } from 'src/app/services/SST/data.service';
import { UtilsService } from 'src/app/services/utils.service';

@Component({
  selector: 'app-permisos-pendientes',
  templateUrl: './permisos-pendientes.page.html',
  styleUrls: ['./permisos-pendientes.page.scss'],
})
export class PermisosPendientesPage implements OnInit {
  pendientes: EDGuardarPermisoTrabajoAPP[] = [];

  constructor(
    private data: DataBaseService,
    private utils: UtilsService,
    private router: Router,
    private dataService: DataService,
    private alertControoler: AlertController
  ) {}

  ngOnInit() {
    this.getPermisosPendientes();
  }

  async getPermisosPendientes() {
    try {
      await this.utils.presentLoading();
      this.data.open().then(async () => {
        this.data.getAllEDGuardarPermisoTrabajoAPP().subscribe({
          next: (permisos: EDGuardarPermisoTrabajoAPP[]) => {
            this.utils.dismissLoading();

            if (permisos.length === 0) {
              this.alertControoler.create({}).then(alert => {
                alert.header = 'Informaci√≥n';
                alert.message = 'No hay permisos pendientes';
                alert.buttons = ['Aceptar'];
                alert.present();
              });
            }
            this.pendientes = permisos;
            console.log('Permisos pendientes:', permisos);
          },
          error: error => {
            console.error('Error al obtener los permisos pendientes:', error);
            this.data.close();
          },
          complete: () => {
            console.log('Permisos pendientes completados');
            this.utils.dismissLoading();
          },
        });
      });
    } finally {
      this.utils.dismissLoading();
    }
  }

  continuar(item: EDGuardarPermisoTrabajoAPP) {
    // let ruta = item['rutas'][item['rutas'].length - 1];
    this.router.navigate([item['lastRoute']], { state: { data: item } });
  }

  async enviar(item: EDGuardarPermisoTrabajoAPP) {
    try {
      // this.selectedItemsService.setSelectedItems([]);

      item['strFechaYHoraInicioAct'] = item['strFechaRegistro'];
      await this.utils.presentLoading();
      const { status, data } = await this.dataService.enviarPermisoTrabajo(item);

      this.utils.dismissLoading();

      if (status === 200) {
        this.alertControoler
          .create({
            header: 'Alerta',
            message: 'Permiso enviado correctamente',
            buttons: ['Aceptar'],
          })
          .then(alert => {
            alert.present();
          });
        this.data.open().then(async () => {
          let usuarios = item['listaTrabajadoresMigrados'];
          usuarios.forEach(async usuario => {
            await this.data.deleteTrabajadorMigrado(usuario);
          });
          this.data.deleteEDGuardarPermisoTrabajoAPP(item).then(() => {
            this.utils.dismissLoading();
            this.pendientes = this.pendientes.filter(permiso => permiso !== item);
          });
        });
      } else {
        this.alertControoler
          .create({
            header: 'Alerta',
            message: `Error al enviar el permiso: ${data} `,
            buttons: ['Aceptar'],
          })
          .then(alert => {
            alert.present();
          });
      }
    } catch (error) {
      this.utils.dismissLoading();
      console.error('Error al enviar el permiso:', error);
      this.alertControoler
        .create({
          header: 'Alerta',
          message: 'Error al enviar el permiso intenta m√°s tarde',
          buttons: ['Aceptar'],
        })
        .then(alert => {
          alert.present();
        });
    }
  }

  public formatearFecha(fecha: string): string {
    const date = new Date(fecha);

    const dia = date.getDate();
    const mes = date.getMonth() + 1;
    const anio = date.getFullYear();

    const hora = date.getHours().toString().padStart(2, '0');
    const minutos = date.getMinutes().toString().padStart(2, '0');

    const fechaFormateada = `${dia.toString().padStart(2, '0')}/${mes.toString().padStart(2, '0')}/${anio.toString()}  ${hora}:${minutos}`;

    return fechaFormateada;
  }
  back() {
    this.router.navigate(['/home/permisos']);
  }
}
